.advanced-search-button span {
  display: inline-block;
  transition: transform .5s;
}

.advanced-search-button span.rotate-arrow {
  transform: rotate(90deg) translate(-1px, -3px);
}

.card-search {
  margin-bottom: 20px;
  border: solid 1px #e6e7f2;
  padding-bottom: 0.25rem;
}

.custom-footer {
  margin-bottom: 20px;
  padding-bottom: 0.25rem;
  border: solid 1px #d0d0d0;
}

.custom-footer label {
  margin-bottom: 0;
}

#contaDebito-addon {
  color: #f00;
}

#contaCredito-addon {
  color: #00f;
}

.grid-lancamentos > .table-area {
  height: auto;
  max-height: inherit;
}

.grid-lancamentos.hidden .table-area,
.grid-lancamentos.hidden .custom-footer,
.grid-lancamentos.hidden [data-testid="searchview-table-footer-lancamentosaldo"] {
  display: none;
}
